.feature-item-outer {
    width: calc(100% / 3);
    padding: 16px;
}

.feature-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 16px;
    padding: 16px;
    width: 100%;
    background-color: #ffffff;
    border: 1px solid #c4c4c4;
    border-radius: 8px;
    transition: all ease-in-out 0.3s;
}

.feature-item:hover {
    transform: translateY(-10px);
}

.feature-item img {
    width: 100px;
    height: 100px;
}

.feature-item h2 {
    font-size: 1.875rem;
    line-height: 1.3;
}

.feature-item p {
    font-size: 1.125rem;
    text-align: center;
}

@media screen and (max-width: 768px) {
    .feature-item-outer {
        width: 100%;
    }
}