#login {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    row-gap: 50px;
    align-items: center;
}

#login form {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
    max-width: 600px;
    padding: 2rem 3rem;
    border: 1px solid #c4c4c4;
    border-radius: 8px;
    background-color: #FFFFFF;
}

#login form label {
    font-size: 1.125rem;
    font-weight: bold;
    margin-bottom: 0.5rem;
}

#login form input {
    height: 48px;
    background: #FFFFFF;
    border: 1px solid #e1e4e8;
    box-shadow: inset 0px 2px 0px rgba(225, 228, 232, 0.2);
    border-radius: 5px;
    text-indent: 7px;
    line-height: 1.25;
    color: #444;
    margin-bottom: 2rem;
}

#login form button {
    align-self: center;
    min-width: fit-content;
    width: 50%;
}

