#parental-control {
    display: flex;
    flex-direction: column;
    row-gap: 40px;
    column-gap: 40px;
    background-color: #f0f0f0;
    padding: 50px;
    border-radius: 20px;
    box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
  }
  
  #parental-control .device-container {
    display: flex;
    flex-direction: column;
    row-gap: 2.5rem;
  }
  
  @media (max-width: 768px) {
    #parental-control {
      padding: 30px;
    }
  }
  