
body {
    font-family: Arial, sans-serif;
    font-size: 16px;
    line-height: 1.5;
  }
  
#header {
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    background-color: #ffffff;
    border-bottom: 1px solid #c4c4c4;
    z-index: 5;
    height: 121px;
    padding: 0 60px;
    display: flex;
    align-items: stretch;
}

#header .title {
    flex: 1 1;
    display: flex;
    align-items: center;
}

#header .title a {
    font-size: 2.25rem;
    font-weight: bold;
}

#header .options {
    display: flex;
    align-items: stretch;
    row-gap: 30px;
    column-gap: 30px;
    background-color: #ffffff;
}

#header .options .menu {
    list-style: none;
    display: flex;
    align-items: stretch;
    font-size: 1.5rem;
    line-height: 3.5rem;
    row-gap: 16px;
    column-gap: 16px;
}

#header .options .menu>li {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    cursor: pointer;
    position: relative;
}

#header .options .menu>li:hover::after,
#header .options .menu>li.active::after {
    position: absolute;
    content: "";
    background-color: #073628;
    bottom: 0;
    left: 0;
    right: 0;
    height: 5px;
}

#header .options .menu>li:hover span svg {
    transform: rotateZ(180deg);
}

#header .options .menu>li>* {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 8px;
}

#header .options .menu .submenu {
    list-style: none;
    display: none;
    align-items: flex-start;
    position: absolute;
    top: 100%;
    flex-direction: column;
    font-size: 1.125rem;
    width: max-content;
    padding: 30px 0;
    row-gap: 16px;
    width: 100%;
}

#header .options .menu .submenu::before {
    position: absolute;
    content: "";
    background-color: #ffffff;
    left: -100vw;
    right: -100vw;
    top: 0.5px;
    bottom: 0;
    z-index: -1;
}

#header .options .menu>li:hover .submenu {
    display: flex;
}

#header .options .menu .submenu li:hover {
    color: #073628;
}

#header .buttons {
    display: flex;
    row-gap: 16px;
    column-gap: 16px;
    align-items: center;
}

#header .buttons.hidden {
    display: none;
}

#header .buttons.hidden svg {
    font-size: 2rem;
    cursor: pointer;
}


@media screen and (max-width: 1024px) {
    #header .options {
        position: absolute;
        top: calc(100% + 0.5px);
        left: -100%;
        right: 100%;
        height: calc(100vh - 121px);
        overflow: auto;
        flex-direction: column;
        align-items: stretch;
        padding: 30px 60px;
        transition: all ease-in-out 0.3s;
    }

    #header .options.expand {
        left: 0;
        right: 0;
    }

    #header .options .menu {
        flex-direction: column;
        align-items: stretch;
    }

    #header .options .menu>li:hover::after {
        content: none;
    }

    #header .options .menu>li.active::after {
        content: "";
    }

    #header .options .menu>li>* {
        border-bottom: 1px solid #c1c1c1;
    }

    #header .options .menu .submenu {
        position: initial;
    }

    #header .options .menu .submenu::before {
        content: none;
    }

    #header .options .buttons {
        flex-direction: column;
    }

    #header .options .buttons button {
        min-width: 250px;
        width: 50%;
    }

    #header .buttons.hidden {
        display: flex;
    }
}

@media screen and (max-width: 768px) {
    #header {
        padding: 0 30px;
    }

    #header .title a {
        font-size: 1.5rem;
        font-weight: bold;
    }

    #header .options {
        padding: 30px;
    }

    #header .buttons.hidden svg {
        font-size: 1rem;
    }
}

@media screen and (max-width: 425px) {}