#hero-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 40px;
  background-color: #F4F4F4;
  padding: 100px 50px;
}

#hero-area .heading {
  text-align: left;
  font-family: "Bauhaus 93", sans-serif;
  font-size: 5rem;
  color: #073628;
  margin-bottom: 20px;
}

#hero-area p {
  max-width: 800px;
  font-family: "Helvetica Neue", sans-serif;
  font-size: 1.2rem;
  color: #000;
  line-height: 1.5;
}

#hero-area button {
  border: 2px solid #073628;
  background-color: #073628;
  color: #fff;
  font-family: "Helvetica Neue", sans-serif;
  font-size: 1rem;
  padding: 15px 30px;
  border-radius: 50px;
  transition: all 0.3s ease-in-out;
}

#hero-area button:hover {
  background-color: transparent;
  color: #073628;
}

@media only screen and (max-width: 768px) {
  #hero-area {
    padding: 80px 30px;
    row-gap: 20px;
  }

  #hero-area .heading {
    text-align: center;
    font-size: 3rem;
    margin-bottom: 10px;
  }

  #hero-area p {
    max-width: 600px;
    font-size: 1rem;
    text-align: center;
  }

  #hero-area button {
    margin: auto;
    display: block;
  }
}
