#qr-code {
    display: flex;
    align-items: center;
    justify-content: center;
    row-gap: 40px;
    column-gap: 40px;
    background-color: #F5F5F5;
    border-radius: 20px;
    padding: 20px;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
}

#qr-code img {
    height: 65vmin;
    aspect-ratio: 1;
    border-radius: 8px;
}

@media screen and (max-width: 1024px) {
    #qr-code {
        flex-direction: column;
    }
}
