footer {
    color: #ffffff;
    background-color: #202020;
}

footer > div {
    padding: 20px 60px;
}

.footer-bottom {
    background-color: #000000;
    display: flex;
    justify-content: space-between;
}

.footer-bottom ul {
    list-style: none;
    display: flex;
    align-items: center;
    font-size: 0.6875rem;
}

.footer-bottom ul.left li {
    padding: 0 10px;
}

.footer-bottom ul.right {
    column-gap: 16px;

}

.footer-bottom ul.left li:not(:last-child) {
    border-right: 1px solid #ffffff;
}

.footer-bottom ul li svg {
    font-size: 1.25rem;
}

@media screen and (max-width: 1024px) {
    
}

@media screen and (max-width: 768px) {
    footer > div {
        padding: 20px 30px;
    }

    .footer-bottom {
        flex-direction: column-reverse;
        row-gap: 30px;
    }
}

@media screen and (max-width: 425px) {
    .footer-bottom ul {
        font-size: 1.25rem;
    }

    .footer-bottom ul.left {
        flex-direction: column;
        align-items: flex-start;
        row-gap: 16px;
    }

    .footer-bottom ul.left li {
        padding: 0;
    }

    .footer-bottom ul.left li:not(:last-child) {
        border-right: none;
    }

    .footer-bottom ul li svg {
        font-size: 1.75rem;
    }
}