#dashboard {
    display: flex;
    flex-direction: column;
    row-gap: 40px;
    column-gap: 40px;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.2),
      -4px -4px 8px rgba(255, 255, 255, 0.5);
    background-color: #e6e6e6;
  }
  
  #dashboard .device-container {
    display: flex;
    flex-direction: column;
    row-gap: 2.5rem;
    padding: 1.5rem;
    border-radius: 16px;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.2),
      -4px -4px 8px rgba(255, 255, 255, 0.5);
    background-color: #f2f2f2;
  }
  
  @media screen and (max-width: 768px) {
    #dashboard {
      row-gap: 20px;
      column-gap: 20px;
    }
  
    #dashboard .device-container {
      row-gap: 1.5rem;
    }
  }
  