.description-item {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 16px;
    padding: 16px;
}

.description-item img {
    width: 100%;
}

.description-item h2 {
    font-size: 1.875rem;
    line-height: 1.3;
}

.description-item p {
    font-size: 1.125rem;
    text-align: center;
}