
body {
    font-family: Arial, sans-serif;
    font-size: 16px;
    line-height: 1.5;
  }
  
#description {
    display: flex;
    flex-direction: column;
    row-gap: 50px;
    align-items: center;
}

#description .description-container {
    display: flex;
    column-gap: 40px;
}

@media screen and (max-width: 768px) {
    #description .description-container {
        flex-direction: column;
        row-gap: 40px;
    }
}
