body {
    font-family: Arial, sans-serif;
}

#signup {
    display: flex;
    flex-direction: column;
    row-gap: 50px;
    align-items: center;
}

#signup form {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
    max-width: 600px;
    padding: 2rem 3rem;
    border: 1px solid #c4c4c4;
    border-radius: 8px;
    background-color: #FFFFFF;
}

#signup form label {
    font-size: 1.125rem;
    font-weight: Bold;
}

#signup form > label {
    margin-bottom: 0.5rem;
}

#signup form input {
    height: 48px;
    background: #FFFFFF;
    border: 2px solid #e1e4e8;
    box-shadow: inset 0px 2px 0px rgba(225, 228, 232, 0.2);
    border-radius: 5px;
    text-indent: 7px;
    line-height: 1.25;
    color: #505050;
}

#signup form > input {
    margin-bottom: 2rem;
}

#signup form .gender-group {
    margin-top: 8px;
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
}

#signup form .gender-group .radio-btn {
    flex: 1 1;
    display: flex;
    align-items: center;
    column-gap: 10px;
}

#signup form .gender-group .radio-btn input {
    height: auto;
}

#signup form button {
    align-self: center;
    min-width: fit-content;
    width: 50%;
}

@media screen and (max-width: 1024px) {
    
}

@media screen and (max-width: 768px) {
    #signup form {
        padding:30px;
    }

    #signup form .gender-group {
        flex-direction: column;
        align-items: flex-start;
        margin-left: 20px;
        row-gap: 8px;
    }
}

