.device {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
    background-color: #ffffff;
    border-radius: 16px;
    box-shadow: 0 0 3px 0 #000000;
}

.device .device-name {
    font-size: 1.5rem;
}

.device.placeholder .device-name {
    font-size: 2rem;
    text-align: center;
}

.device .device-name span {
    font-weight: bold;
}

.device .categories {
    display: flex;
    flex-wrap: wrap;
    row-gap: 8px;
}

.device .category {
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    align-items: center;
    width: 100%;
}

.device .category .category-name {
    font-size: 1.25rem;
    margin-right: 20px;
}

#parental-control .update-btn {
    align-self: flex-end;
    min-width: 200px;
}